import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import CardView from '../components/CardView';
import pickCard from '../logic/pickCard';

export class ThreeCard extends Component {
    static displayName = ThreeCard.name;

    constructor(props) {
        super(props);

        this.state = {
            card1Index: null,
            card1Meaning: null,
            card2Index: null,
            card2Meaning: null,
            card3Index: null,
            card3Meaning: null
        };

    }

    handleClick = () => {

        var cardsInfo = pickCard(3);
        var Card1 = cardsInfo.Cards[0];
        var Card2 = cardsInfo.Cards[1];
        var Card3 = cardsInfo.Cards[2];

        this.setState(
            {

                card1Index: Card1.cardIndex,
                card1Meaning: (Card1.isReversed ? 'R' : 'N'),
                card2Index: Card2.cardIndex,
                card2Meaning: (Card2.isReversed ? 'R' : 'N'),
                card3Index: Card3.cardIndex,
                card3Meaning: (Card2.isReversed ? 'R' : 'N')

            }
        );

    }

    render() {
        return (
            <div>
                <div class='header'>
                    <Link to="/">Touch Tarot</Link>
                </div>
                <button onClick={this.handleClick} className="menuitem">
                    Draw 3 Cards
                </button>
                <div>
                    <CardView cardMeaning={this.state.card1Meaning} cardIndex={this.state.card1Index} cardTitle={"Past"} />
                    <CardView cardMeaning={this.state.card2Meaning} cardIndex={this.state.card2Index} cardTitle={"Present"} />
                    <CardView cardMeaning={this.state.card3Meaning} cardIndex={this.state.card3Index} cardTitle={"Future"} />
                </div>
            </div>
        );
    }
}
