export default function pickCard(numCards) {

    let cardsInfo = {
        Prayer: "Love surrounds me. Faith supports me. Wisdom guides me.",
        Cards: []
    };

    for (var i = 0; i < numCards; i++) {

        let lookingForCard = true;

        while (lookingForCard) {

            //a random number between 0 and 77
            let cardIndex = Math.floor((Math.random() * 78));

            //if random number between 0 and 1 = 1
            let isReversed = (Math.floor((Math.random() * 2)) === 1);


            console.log('[' + i + '] Random Index: ' + cardIndex);


            //if this card already in list?
            let isInList = false;
            cardsInfo.Cards.forEach(function (CInfo) {
                if (CInfo.cardIndex == cardIndex) {
                    isInList = true;
                    console.log('!!! Dup Found !!!: ' + cardIndex);
                }
            });

            if (!isInList) {
                lookingForCard = false;
                cardsInfo.Cards[i] = { cardIndex: cardIndex, isReversed: isReversed};
            }

        }



    }


    return cardsInfo;


    ////a random number between 0 and 77
    //let cardIndex = Math.floor((Math.random() * 78));

    ////if random number between 0 and 1 = 1
    //let isReversed = (Math.floor((Math.random() * 2)) === 1);

    //return {
    //    prayer: Prayer,
    //    cardIndex: cardIndex,
    //    isReversed: isReversed,
    //};

}

