import React, { Component } from 'react';
import { Link } from 'react-router-dom';

export class Home extends Component {
    static displayName = Home.name;

    render() {

        let imgSrc1 = require('../img/MenuIconOne.png');
        let imgSrc3 = require('../img/MenuIconThree.png');
        let imgSrcB = require('../img/MenuIconBrowse.png');

        return (
            <div>
                <div class='header'>Touch Tarot</div>
                <div sytle='display:block;'>

                    <Link to="/onecard" class='menuitem'><img src={imgSrc1} alt='One Card'></img>One Card</Link>
                    <Link to="/threecard" class='menuitem'><img src={imgSrc1} alt='One Card'></img>Three Card</Link>
                    <Link to="/browse" class='menuitem'><img src={imgSrc1} alt='One Card'></img>Browse</Link>

                    {/*<a href='onecard' class='menuitem'><img src={imgSrc1} alt='One Card'></img>One Card</a>*/}
                    {/*<a href='threecard' class='menuitem'><img src={imgSrc3} alt='Three Cards'></img>Three Card</a>*/}
                    {/*<a href='browse' class='menuitem'><img src={imgSrcB} alt='Browse'></img>Browse</a>*/}

                </div>
            </div>
        );
    }
}
