import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import CardView from '../components/CardView';
import pickCard from '../logic/pickCard';

export class OneCard extends Component {
    static displayName = OneCard.name;

    constructor(props) {
        super(props);

        this.state = {
            cardIndex: null,
            cardMeaning: null
        };

    }

    handleClick = () => {

        var theCard = pickCard(1).Cards[0];
        this.setState(
            {
                cardIndex: theCard.cardIndex,
                cardMeaning: (theCard.isReversed ? 'R' : 'N')
            }
        );

    }

    render() {
        return (
            <div>
                <div class='header'>
                    {/*<div style={{ cursor: "pointer" }} onClick={event => window.location.href = '/tt2'}>Touch Tarot</div>*/}
                    <Link to="/">Touch Tarot</Link>
                </div>
                <button onClick={this.handleClick} className="menuitem">
                    Draw A Card
                </button>
                <div>
                    <CardView cardMeaning={this.state.cardMeaning} cardIndex={this.state.cardIndex} />
                </div>
            </div>
        );
    }
}
