import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import CardView from '../components/CardView';

export class Browse extends Component {
    static displayName = Browse.name;

    constructor(props) {
        super(props);

        this.state = {
            browseState: "H" //Home
        };

    }

    handleClick = (newState) => {

        console.log("handleClick:" + newState);

        this.setState(
            {
                browseState: newState
            }
        );
    }

    render() {

        console.log("render:" + this.state.browseState);

        switch (this.state.browseState) {

            case "H": //Home
                return (
                    <div>
                        <div class='header'>
                            <Link to="/">Touch Tarot</Link>
                        </div>
                        <div sytle='display:block;'>
                            <button onClick={() => this.handleClick('M')} class='menuitem'>Major Arcana</button>
                            <button onClick={() => this.handleClick('W')} class='menuitem'>Wands</button>
                            <button onClick={() => this.handleClick('C')} class='menuitem'>Cups</button>
                            <button onClick={() => this.handleClick('S')} class='menuitem'>Swords</button>
                            <button onClick={() => this.handleClick('P')} class='menuitem'>Pentacles</button>
                        </div>
                    </div>
                );

            case "M": //Major
                return (
                    <div>
                        <div class='header'>
                            <Link to="/">Touch Tarot</Link>
                        </div>
                        <div sytle='display:block;'>
                            <button onClick={() => this.handleClick('H')} class='menuitem'>{ '< Back To Suites' }</button>
                            <button onClick={() => this.handleClick('0')} class='menuitem'>0. The Fool</button>
                            <button onClick={() => this.handleClick('1')} class='menuitem'>I. The Magician</button>
                            <button onClick={() => this.handleClick('2')} class='menuitem'>II. The High Priestess</button>
                            <button onClick={() => this.handleClick('3')} class='menuitem'>III. The Empress</button>
                            <button onClick={() => this.handleClick('4')} class='menuitem'>VI. The Emperor</button>
                            <button onClick={() => this.handleClick('5')} class='menuitem'>V. The Hierophant</button>
                            <button onClick={() => this.handleClick('6')} class='menuitem'>VI. The Lovers</button>
                            <button onClick={() => this.handleClick('7')} class='menuitem'>VII. The Chariot</button>
                            <button onClick={() => this.handleClick('8')} class='menuitem'>VIII. Strength</button>
                            <button onClick={() => this.handleClick('9')} class='menuitem'>IX. The Hermit</button>
                            <button onClick={() => this.handleClick('10')} class='menuitem'>X. Wheel of Fortune</button>
                            <button onClick={() => this.handleClick('11')} class='menuitem'>XI. Justice</button>
                            <button onClick={() => this.handleClick('12')} class='menuitem'>XII. The Hanged Man</button>
                            <button onClick={() => this.handleClick('13')} class='menuitem'>XIII. Death</button>
                            <button onClick={() => this.handleClick('14')} class='menuitem'>XIV. Temperance</button>
                            <button onClick={() => this.handleClick('15')} class='menuitem'>XV. The Devil</button>
                            <button onClick={() => this.handleClick('16')} class='menuitem'>XVI. The Tower</button>
                            <button onClick={() => this.handleClick('17')} class='menuitem'>XVII. The Star</button>
                            <button onClick={() => this.handleClick('18')} class='menuitem'>XVIII. The Moon</button>
                            <button onClick={() => this.handleClick('19')} class='menuitem'>XIX. The Sun</button>
                            <button onClick={() => this.handleClick('20')} class='menuitem'>XX. Judgement</button>
                            <button onClick={() => this.handleClick('21')} class='menuitem'>XXI. The World</button>
                        </div>
                    </div>
                );

            case "W": //Wands
                return (
                    <div>
                        <div class='header'>
                            <Link to="/">Touch Tarot</Link>
                        </div>
                        <div sytle='display:block;'>
                            <button onClick={() => this.handleClick('H')} class='menuitem'>{ '< Back To Suites' }</button>
                            <button onClick={() => this.handleClick('22')} class='menuitem'>Ace of Wands</button>
                            <button onClick={() => this.handleClick('23')} class='menuitem'>Two of Wands</button>
                            <button onClick={() => this.handleClick('24')} class='menuitem'>Three of Wands</button>
                            <button onClick={() => this.handleClick('25')} class='menuitem'>Four of Wands</button>
                            <button onClick={() => this.handleClick('26')} class='menuitem'>Five of Wands</button>
                            <button onClick={() => this.handleClick('27')} class='menuitem'>Six of Wands</button>
                            <button onClick={() => this.handleClick('28')} class='menuitem'>Seven of Wands</button>
                            <button onClick={() => this.handleClick('29')} class='menuitem'>Eight of Wands</button>
                            <button onClick={() => this.handleClick('30')} class='menuitem'>Nine of Wands</button>
                            <button onClick={() => this.handleClick('31')} class='menuitem'>Ten of Wands</button>
                            <button onClick={() => this.handleClick('32')} class='menuitem'>Page of Wands</button>
                            <button onClick={() => this.handleClick('33')} class='menuitem'>Knight of Wands</button>
                            <button onClick={() => this.handleClick('34')} class='menuitem'>Queen of Wands</button>
                            <button onClick={() => this.handleClick('35')} class='menuitem'>King of Wands</button>
                        </div>
                    </div>
                );

            case "C": //Cups
                return (
                    <div>
                        <div class='header'>
                            <Link to="/">Touch Tarot</Link>
                        </div>
                        <div sytle='display:block;'>
                            <button onClick={() => this.handleClick('H')} class='menuitem'>{ '< Back To Suites' }</button>
                            <button onClick={() => this.handleClick('36')} class='menuitem'>Ace of Cups</button>
                            <button onClick={() => this.handleClick('37')} class='menuitem'>Two of Cups</button>
                            <button onClick={() => this.handleClick('38')} class='menuitem'>Three of Cups</button>
                            <button onClick={() => this.handleClick('39')} class='menuitem'>Four of Cups</button>
                            <button onClick={() => this.handleClick('40')} class='menuitem'>Five of Cups</button>
                            <button onClick={() => this.handleClick('41')} class='menuitem'>Six of Cups</button>
                            <button onClick={() => this.handleClick('42')} class='menuitem'>Seven of Cups</button>
                            <button onClick={() => this.handleClick('43')} class='menuitem'>Eight of Cups</button>
                            <button onClick={() => this.handleClick('44')} class='menuitem'>Nine of Cups</button>
                            <button onClick={() => this.handleClick('45')} class='menuitem'>Ten of Cups</button>
                            <button onClick={() => this.handleClick('46')} class='menuitem'>Page of Cups</button>
                            <button onClick={() => this.handleClick('47')} class='menuitem'>Knight of Cups</button>
                            <button onClick={() => this.handleClick('48')} class='menuitem'>Queen of Cups</button>
                            <button onClick={() => this.handleClick('49')} class='menuitem'>King of Cups</button>
                        </div>
                    </div>
                );


            case "S": //Swords
                return (
                    <div>
                        <div class='header'>
                            <Link to="/">Touch Tarot</Link>
                        </div>
                        <div sytle='display:block;'>
                            <button onClick={() => this.handleClick('H')} class='menuitem'>{ '< Back To Suites' }</button>
                            <button onClick={() => this.handleClick('50')} class='menuitem'>Ace of Swords</button>
                            <button onClick={() => this.handleClick('51')} class='menuitem'>Two of Swords</button>
                            <button onClick={() => this.handleClick('52')} class='menuitem'>Three of Swords</button>
                            <button onClick={() => this.handleClick('53')} class='menuitem'>Four of Swords</button>
                            <button onClick={() => this.handleClick('54')} class='menuitem'>Five of Swords</button>
                            <button onClick={() => this.handleClick('55')} class='menuitem'>Six of Swords</button>
                            <button onClick={() => this.handleClick('56')} class='menuitem'>Seven of Swords</button>
                            <button onClick={() => this.handleClick('57')} class='menuitem'>Eight of Swords</button>
                            <button onClick={() => this.handleClick('58')} class='menuitem'>Nine of Swords</button>
                            <button onClick={() => this.handleClick('59')} class='menuitem'>Ten of Swords</button>
                            <button onClick={() => this.handleClick('60')} class='menuitem'>Page of Swords</button>
                            <button onClick={() => this.handleClick('61')} class='menuitem'>Knight of Swords</button>
                            <button onClick={() => this.handleClick('62')} class='menuitem'>Queen of Swords</button>
                            <button onClick={() => this.handleClick('63')} class='menuitem'>King of Swords</button>
                        </div>
                    </div>
                );


            case "P": //Pentacles
                return (
                    <div>
                        <div class='header'>
                            <Link to="/">Touch Tarot</Link>
                        </div>
                        <div sytle='display:block;'>
                            <button onClick={() => this.handleClick('H')} class='menuitem'>{ '< Back To Suites' }</button>
                            <button onClick={() => this.handleClick('64')} class='menuitem'>Ace of Pentacles</button>
                            <button onClick={() => this.handleClick('65')} class='menuitem'>Two of Pentacles</button>
                            <button onClick={() => this.handleClick('66')} class='menuitem'>Three of Pentacles</button>
                            <button onClick={() => this.handleClick('67')} class='menuitem'>Four of Pentacles</button>
                            <button onClick={() => this.handleClick('68')} class='menuitem'>Five of Pentacles</button>
                            <button onClick={() => this.handleClick('69')} class='menuitem'>Six of Pentacles</button>
                            <button onClick={() => this.handleClick('70')} class='menuitem'>Seven of Pentacles</button>
                            <button onClick={() => this.handleClick('71')} class='menuitem'>Eight of Pentacles</button>
                            <button onClick={() => this.handleClick('72')} class='menuitem'>Nine of Pentacles</button>
                            <button onClick={() => this.handleClick('73')} class='menuitem'>Ten of Pentacles</button>
                            <button onClick={() => this.handleClick('74')} class='menuitem'>Page of Pentacles</button>
                            <button onClick={() => this.handleClick('75')} class='menuitem'>Knight of Pentacles</button>
                            <button onClick={() => this.handleClick('76')} class='menuitem'>Queen of Pentacles</button>
                            <button onClick={() => this.handleClick('77')} class='menuitem'>King of Pentacles</button>
                        </div>
                    </div>
                );


            default:

                let cardIndex = parseInt(this.state.browseState);

                let PrevState = 'H'; //Home (will be overwritten here)
                let backText = '< Back To Home'; //Home (will be overwritten here)

                if (cardIndex >= 64) {
                    PrevState = 'P';
                    backText = '< Back to Pentacles';
                }
                else if (cardIndex >= 50) {
                    PrevState = 'S';
                    backText = '< Back to Swords';
                }
                else if (cardIndex >= 36) {
                    PrevState = 'C';
                    backText = '< Back to Cups';
                }
                else if (cardIndex >= 22) {
                    PrevState = 'W';
                    backText = '< Back to Wands';
                }
                else {
                    PrevState = 'M';
                    backText = '< Back to Major Arcana';

                }

                return (
                    <div>
                        <div class='header'>
                            <Link to="/">Touch Tarot</Link>
                        </div>
                        <div sytle='display:block;'>
                            <button onClick={() => this.handleClick(PrevState)} class='menuitem'>{backText}</button>
                        </div>
                        <div>
                            <CardView cardMeaning="B" cardIndex={cardIndex} />
                        </div>
                    </div>
                );
        }

    }
}
